import React, { useContext, useState, useEffect } from 'react';
import { Container } from 'react-bootstrap';
import Fade from 'react-reveal/Fade';
import { Link } from 'react-scroll';
import PortfolioContext from '../../context/context';

const Header = () => {
  const { hero } = useContext(PortfolioContext);
  const { img, title, name, subtitle, cta } = hero;

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="hero">
      <Container className="container-adjust">
        <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={500} distance="30px">
          <div className="row">
             <div className="about-wrapper__image">
              </div>
            <h4 className="float-left font-weight-light grey-text">Hi, my name is</h4>
          </div>
          <div className="row">
            <h1 className="hero-title mt-3 ml-0 pl-0 white-text" align="left">
              {name || 'Eder Mazariegos'}.
              <br />
              <span className="grey-text">{subtitle || "A Full-Stack web developer."}</span>
            </h1>
          </div>
        </Fade>
        <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={1000} distance="30px">
          <div className="row mb-5">
            <div className="col-7 ml-0 pl-0">
              <p className="grey-text" align="left">
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam 
                nonumy eirmod tempor invidunt ut labore et dolore magna 
                aliquyam erat, sed diam voluptua.
              </p>
            </div>
          </div>
        </Fade>
        <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={1000} distance="30px">
          <div className="row">
            <p className="hero-cta ml-0">
              <span className="cta-btn cta-btn--hero">
                <Link to="about" smooth duration={1000}>
                  {cta || 'Get in touch'}
                </Link>
              </span>
            </p>
          </div>
        </Fade>
      </Container>
    </section>
  );
};

export default Header;
